import React from 'react';

function TweetInput(props) {
  // Ë¶ÅÁ¥†„Å´„Ç¢„ÇØ„Çª„Çπ„Åô„Çã„Åü„ÇÅ„ÅÆÂèÇÁÖß„ÇíÂèñÂæó
  // ref.current„ÅÆ„Éá„Éï„Ç©„É´„ÉàÂÄ§„ÅØnull„Å´„Åó„Å¶„Åä„Åè
  // ÂèÇÁÖß„Åó„Åü„ÅÑË¶ÅÁ¥†„Å´JSX„ÅÆÊñπ„Åß„Åì„ÅÆref„Çí„Çª„ÉÉ„Éà„Åô„Çã
  const textareaRef = React.useRef(null);

  // Tweet„Éú„Çø„É≥„ÇØ„É™„ÉÉ„ÇØÊôÇ„ÅÆ„Ç≥„Éº„É´„Éê„ÉÉ„ÇØÈñ¢Êï∞„Çí‰Ωú„Å£„Å¶‰øùÂ≠ò
  // textareaRef.current„Åãprops.addTweet„ÅåÊõ¥Êñ∞„Åï„Çå„Åü„Çâ
  // „Ç≥„Éº„É´„Éê„ÉÉ„ÇØÈñ¢Êï∞„Çí‰Ωú„ÇäÁõ¥„Åó
  const sendTweet = React.useCallback(() => {
    // ref„ÅÆcurrent„Å´ÂÖ•„Å£„Å¶„ÅÑ„ÇãÂÄ§„ÅåDOM
    // ÂàùÊúüÂÄ§„ÅØnullÔºà‰∏ä„ÅßÊåáÂÆö„Åó„ÅüÔºâ„Åß„ÄÅ
    // ÂÆüÈöõ„Å´HTMLÂÅ¥„Å´ÊèèÁîª„Åï„Çå„Çã„Å®ÂÄ§„ÅåÂÖ•„Çä„Åæ„Åô
    if(textareaRef.current) {
      props.addTweet({
        id: new Date().getTime(), // ID„ÅØ„É¶„Éã„Éº„ÇØ„Å™ÂÄ§„Å´„Åô„Çã
        icon: '‚ò†Ô∏è', // „Åì„ÅÆ„ÅÇ„Åü„Çä„ÅÆÂÄ§„ÅØÂ•Ω„Åç„Å´„Åó„Å¶„Åè„Å†„Åï„ÅÑ
        displayName: '„Éü„Çπ„Çø„ÉºÊ≠ª',
        accountName: 'mrdeath',
        content: textareaRef.current.value
      });
    }
  }, [props]);

  return (
    <div>
      <div><textarea className="tweet-textarea" ref={textareaRef}></textarea></div>
      <div><button onClick={sendTweet} className="send-tweet">Tweet</button></div>
    </div>
  );
}

export default TweetInput